body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.responsive {
  width: 100%;
}

[hidden], .hidden {
  display: none;
}

img {
  vertical-align: middle;
}

.startLoading {
  color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: -ms-flexbox;
  display: flex;
  font-size: 18px;
  text-align: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color: rgba(0,0,0,.99);
}


