.App {
  margin: 0 auto;
  padding-bottom: 46px;
}

.header {
  padding: 6px 10px;
}

.btns {
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 9;
  left: 0;
  background-color: rgba(0,0,0,.7);
}



.btns button {
  -ms-flex: 1 1;
      flex: 1 1;
  font-size: 15px;
  display: block;
  border: none;
  padding: .65em 0;
  outline: none;
  background-color: transparent;
  color: #fff;
}


.btns button:active {
  opacity: .8;
}

.title, .info {
  color: #999;
  text-align: center;
}

.title {
  color: #999;
  font-size: 14px;
  font-weight: 200;
  margin: 0;
}

.info {
  margin-top: 0;
  margin-bottom: 0;
}

@media screen and (min-width: 480px) {
 .App {
   max-width: 360px;
 } 
}

.menu{
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  padding-bottom: 20px;
  padding-top: 10px;
  color: #fff;
  background-color: rgba(0,0,0,.9);
  overflow-y: scroll;
  z-index: 99;
}

.menu  ul  {
  list-style: none;
  margin: 0 10px;
  padding: 0;
  border-top: 1px solid #fff;
}

.menu  ul li {
font-size: 14px;
padding: 8px 0;
border-bottom: 1px solid #fff;
border-left: 1px solid #fff;
border-right: 1px solid #fff;
text-align: center;
}

.menu  ul li.active {
  color: yellow;
}

.showMenu {
  text-align: center;
  padding: 8px;
  font-size: 16px;
  background-color: #eee;
}


